.message {
    border: 1px solid #ccc;
    padding: 10px;
    margin: 10px 0;
    border-radius: 8px;
}

.imageAndText {
    display: flex;
    align-items: center;
}

.imageAndText img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
}

.text {
    flex: 1;
}

.text:after {
    content: '';
    display: block;
    clear: both;
}

.name {
    font-weight: bold;
}

.messageText {
    white-space: pre-wrap;
}

.time {
    color: #888;
    font-size: 12px;
}