.friendMessage {
    border: 1px solid #ccc;
    padding: 10px;
    margin: 10px 0;
    border-radius: 8px;
}

.friendImageAndText {
    display: flex;
    align-items: center;
}

.friendImageAndText img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
}

.friendText {
    flex: 1;
}

.friendText:after {
    content: '';
    display: block;
    clear: both;
}

.friendName {
    font-weight: bold;
}

.friendMessageText {
    white-space: pre-wrap;
}

.friendTime {
    color: #888;
    font-size: 12px;
}